                    var postJSON = {
                        oauth_token: document.getElementById("oauth_token").value,
                        oauth_verifier: document.getElementById("oauth_verifier").value
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    this.$http.post(url, { postString: objectPostString },
                            function (data) {
                                this.postresult = data;
